import { Component, ViewChild, ElementRef, ViewEncapsulation, AfterViewInit, OnInit } from '@angular/core';
import { LoginService } from './login.service';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { User } from '../foundry/settings/user/user';
import { NotifierService } from 'angular-notifier';
import { ThrowStmt } from '@angular/compiler';
import {MediaMatcher} from '@angular/cdk/layout';
import {ChangeDetectorRef, OnDestroy} from '@angular/core';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit , OnDestroy {
  public user: User;
  public hours = 24;
  mobileQuery: MediaQueryList;
  public now = new Date().getTime();
  private notifier: NotifierService;
  public users;
  private _mobileQueryListener: () => void;
  constructor(public loginService: LoginService, public router: Router, changeDetectorRef: ChangeDetectorRef,
    notifierService: NotifierService, media: MediaMatcher) {
      this.mobileQuery = media.matchMedia('(max-width: 600px)');
      this._mobileQueryListener = () => changeDetectorRef.detectChanges();
      this.mobileQuery.addListener(this._mobileQueryListener);
    this.notifier = notifierService;
    this.router.routeReuseStrategy.shouldReuseRoute = function() {
      return false;
    };
  }

  ngOnInit() {
    this.user = new User();
  }
  userLogin(exampleForm: NgForm) {
    this.loginService.userLogin(this.user).subscribe((data) => {
      this.users = data;
      localStorage.setItem('user', this.users[0].userName);
      localStorage.setItem('role', this.users[0].role);
      localStorage.setItem('timeout', this.now.toString());

      if (this.users[0].roleStatus === 'ACTIVE') {
        if (this.users[0].role === 'SuperAdmin' || this.users[0].role === 'Admin' || this.users[0].role === 'Employee' || this.users[0].role === 'Developer') {
          console.log(data);
          this.router.navigate(['/foundry']);

          exampleForm.reset();
        }
      } else {
        this.notifier.notify('warning', 'This user is not active');
      }
    }, (err) => {
      this.notifier.notify('warning', 'Unauthorized');
    });
  }
  cancel(exampleForm: NgForm) {
    exampleForm.reset();
    this.router.navigate(['']);
  }
  ngOnDestroy(): void {
    this.mobileQuery.removeListener(this._mobileQueryListener);
  }
}
